.container {
  margin: 0 auto;
  width: 55%;
}

.row__one {
  display: grid;
  grid-column-gap: 10px;
  grid-template-rows: 1fr;
  grid-template-columns: 3fr 0.05fr 3fr 3fr 3fr;
  margin: 1rem auto;
}

.row__one:first-of-type {
  margin-top: 0.5rem;
}

.MuiInputLabel-outlined.MuiInputLabel-marginDense {
  transform: translate(14px, 10px) scale(1) !important;
}

.MuiInputLabel-outlined.MuiInputLabel-shrink {
  transform: translate(14px, -5px) scale(0.75) !important;
}

.MuiOutlinedInput-root {
  border-width: 1px !important;
}

.MuiAutocomplete-popupIndicator {
  padding: 4px !important;
  margin-right: -8px !important;
}

.row__two {
  display: grid;
  grid-column-gap: 15px;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 0.05fr 1fr 1fr 1fr 1fr 0.05fr 1fr 1fr;
  margin: 1rem auto;
}

.row__two .MuiOutlinedInput-input {
  padding: 8px;
}

.row__three {
  display: grid;
  grid-column-gap: 20px;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr;
  margin: 1rem auto;
}

.row__four {
  display: grid;
  grid-template-columns: 2fr 1fr 0.1fr;
  grid-column-gap: 10px;
  margin: 1rem auto;
}

#outlined {
  padding: 8px;
}

.row__two .MuiOutlinedInput-inputMarginDense {
  padding: 8px 8px;
}

.row__three .MuiOutlinedInput-inputMarginDense {
  padding: 8px 8px;
}

.buttons {
  display: flex;
  width: 19rem;
  justify-content: space-between;
}
