.sort__row__one {
  display: grid;
  grid-column-gap: 20px;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 1rem auto;
}

.sort__row__one:first-of-type {
  margin-top: 0.5rem;
}

.MuiOutlinedInput-root {
  border-width: 1px !important;
}

.MuiAutocomplete-popupIndicator {
  padding: 4px !important;
  margin-right: -8px !important;
}

.sort__row__two .MuiButtonBase-root {
  padding: 4px !important;
}

.sort__row__two .MuiOutlinedInput-adornedEnd {
  padding-right: 3px !important;
}

.sort__row__two .MuiInputLabel-outlined {
  z-index: 1;
  transform: translate(14px, 10px) scale(1);
  pointer-events: none;
}

.sort__row__two .MuiInputAdornment-root {
  width: 25% !important;
  margin-left: 0 !important;
}

.datepickers {
  display: grid;
  grid-column-gap: 20px;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr;
}

.sort__row__two {
  display: grid;
  grid-column-gap: 20px;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 1rem auto;
}

.sort__row__two .MuiOutlinedInput-input {
  padding: 8px;
}

.sort__row__three {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 1rem auto;
}
